@import "const";

.toggled {
  display: none;

  @media (max-width: $mobile-large) {
    display: block;
    position: absolute;
    top: 16px;
    right: 11px;
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    font-size: 0;
    z-index: 11;
  }

  &:focus {
    outline: none;
  }

  &::after,
  &::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 100%;
    top: 7px;
    left: 0;
    background-color: #000000;
    transition: all 0.3s;
  }

  &::after {
    box-shadow: 0 7px 0 0 #000000, 0 -7px 0 0 $reddish;

    .menu-open & {
      transform: rotate(45deg);
      box-shadow: none;
    }
  }

  &::before {
    .menu-open & {
      transform: rotate(-45deg);
      background: #000000; /* Old browsers */
      background: -moz-linear-gradient(left, #000000 39%, #cc3d3a 100%); /* FF3.6-15 */
      background: -webkit-linear-gradient(left, #000000 39%, #cc3d3a 100%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(to right, #000000 39%, #cc3d3a 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr= "#000000", endColorstr="#cc3d3a", GradientType=1); /* IE6-9 */
    }
  }
}
